.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;


  grid-template-areas:
    "h  h"
    "n  c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px;*/

}

.header {
  grid-area: h;
  background-color: blueviolet;
}

.header img {
  width: 20px;
}

.nav {
  grid-area: n;
  background-color: aqua;
}

.content {
  grid-area: c;
  background-color: rgb(180, 199, 111);
}